@import url(https://fonts.googleapis.com/css2?family=Raleway&family=Prompt:wght@900&Raleway:ital,wght@0,800;1,200&family=Courgette&display=swap);


ul {
  position: relative;
  right: 22px;
  list-style-type: none;
  margin: 25px auto 0;
  z-index: 1;
}

li {
  margin: 15px 0;
  color: white;
}

li a {
  color: white;
}

li a:hover {
  -webkit-filter: sepia(30%);
          filter: sepia(30%);
}

.icon {
  width: 25px;
  height: 25px;
  position: relative;
  right: 4px;
  text-align: center;
}

.pannello-laterale {
  top: 0;
  right: 0;
  width: 50px;
  height: 150vh;
  background-color: #cc4a55d3;
  position: fixed;  
  box-shadow: 0 4px 8px 0 #ffffff;
}

.rossa {
  background-color: #cc4a55d3;
}

.arancione {  
  background-color: #ffaa0bb7;
}

.nessuno {
  background-color: #3f612db9;
}

.separatore-icone {
  position: relative;
  top: 5px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.404);
  width: 70%;
}

.testo-attenzione {
  position: relative;
  right: 125px;
  width: 300px;
  -webkit-animation: anim 15s infinite;
          animation: anim 15s infinite;
  color: #FFF7EA;
  font-weight: bold;
  font-size: 1.2em;
  z-index: -1;
}

@-webkit-keyframes anim {
  0% {
    transform: translateY(-100vh) rotate(90deg);
  }
  50% {
    transform: translateY(50vh) rotate(90deg);
  }
  100% {
    transform: translateY(150vh) rotate(90deg);
  }
}

@keyframes anim {
  0% {
    transform: translateY(-100vh) rotate(90deg);
  }
  50% {
    transform: translateY(50vh) rotate(90deg);
  }
  100% {
    transform: translateY(150vh) rotate(90deg);
  }
}
.data-superiore {
  font-size: 4em;
  color: #cc4a5528;  
  font-family: 'Prompt';
}

.data-inferiore {
  font-size: 3em;
  position: relative;
  top: -50px;
  right: 15px;
  color: #cc4a54;
  font-weight: bold;
  font-family: 'Raleway';
}

.titolo-data {
  width: 85%;
  text-align: right;
}

.covid-testo {
  font-size: 0.9em;
  font-weight: bold;
  position: relative;
  top: -55px;
}
hr {
  width: 75px;
  border-bottom: 1.3px solid#cc4a559f;
  position: relative;
  top: -55px;
}

.illustrazioni {
  width: 250px;
  position: relative;
  top: -25px;
  z-index: -1;
}

strong {
  color:#db222b;
}

.primo-titolo {
  text-align: center;
  font-size: 2em;
  margin: 0 auto;
  padding: 10px 0;
  font-family: 'Prompt', sans-serif;
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px 1px #000000;
  opacity: 1;
  background-color: rgba(63, 97, 45, 0.336);
  border-radius: 10px;
  width: 83%;  
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.secondo-titolo {
  font-weight: bold;
  font-size: 3.5em;
  position: relative;
  top: -37px;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  color:  rgba(37, 37, 37, 0.918);
}

.secondo-titolo:hover {
  color: #cc4a55;
  cursor: pointer;
}

.tavola {
  width: 85%;
  text-align: center;
  margin: 0 0 35px;
  border-radius: 15px;
}

.cornice-testo {  
  background-color: #ffffff;
  border-radius: 13px;
  padding: 20px 25px 10px;
  position: relative;
  top: -20px;  
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  width: 14em;
  margin: 0 auto;
}

.cornice-testo:hover{
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.animate__animated {
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}
.footer {
  width: 85%;
  text-align: justify;
  font-size: 0.8em;
}

.footer p:nth-child(2) {
  text-align: center;
}
a {
  color:rgba(0, 0, 0, 0.5);
  font-weight: bold;
}

a:hover {  
  text-decoration: none;
}

a:hover {
  color: #cc4a55d3;
}
a:active {
  color:  #5c8408; 
}

body {
  background-color: #FFF7EA;
  height: auto;
  background-repeat: no-repeat;
  height: 100%;
}

p {  
  font-family: 'Raleway', sans-serif;
  color: #221E22;
}
